.fg {
  position: relative;
  margin: 0;
  input{
    border-radius: 0;
    border: none;
    border-bottom: 1px solid #666;
    padding: 0;
    height: 3.8em;
    width: 100%;
    font-size: 1.25em;
    background-color: transparent;
    color: $black;
    font-weight: 300;
    &::placeholder {
      color: $dark;
    }
    &:focus {
      border-color: $primary;
    }
  }
}

.m-radio,
.m-check{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.m-check,
.m-radio{
  label{
    position: relative;
    cursor: pointer;
    display: flex;
    align-items: center;
    padding-left: 2.5em;
    min-height: 1.625em;
    color: #999;
    font-weight: 300;
    span{
      font-size: .875em;
      line-height: 125%;
    }
    &:before,
    &:after{
      pointer-events: none;
    }
    &:before{
      content: '';
      position: absolute;
      z-index: 1;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      border: 1px solid $dark;
      width: 1.625em;
      height: 1.625em;
      @include bgImgSize(0);
      transition: all .0s;
    }
    a{
      text-decoration: underline;
      position: relative;
      z-index: 2;
    }
  }
}

.m-check {
  input {
    &:checked+label {
      &::before {
        background-size: 1.125em auto;
      }
    }
  }
  label {
    &:before {
      border-radius: .1875em;
      background-image: url(../img/check.svg);
    }
  }
}


@media screen and (max-width: 980px){
  .fg{
    input{
      font-size: 1em;
      height: 3.5em;
    }
  }

  .m-check{
    input {
      &:checked+label {
        &::before {
          background-size: .8125em auto;
        }
      }
    }
    label{
      padding-left: 1.9375em;
      &:before{
        width: 1.1875em;
        height: 1.1875em;
      }
      span{
        font-size: .75em;
        line-height: 125%;
      }
    }
  }
}


