.works{
  padding: 11.625em 0 4em;
}

.work-info{
  align-items: flex-end;
  &__desc{
    margin-top: .65em;
    color: #9f9f9f;
    font-weight: 300;
  }
}

.work-detail{
  padding: 5em 0 6.25em;
  background-color: #f0f0f0;
  margin-top: 4.625em;
  &__items{
    @include grid100gap(5em);
  }
  &__img{
    display: block;
    width: 100%;
    &--hide-desktop{
      display: none;
    }
  }
}

.info{
  padding: 2.25em 0 6.25em;
  &__col2{
    max-width: 40em;
  }
  &__desc{
    font-size: 1.25em;
    line-height: 130%;
    font-weight: 300;
    color: $dark;
  }
  &__nav{
    display: flex;
    margin-top: 3.5em;
  }
  &__link{
    svg{
      margin-left: 1em;
    }
  }
}


@media screen and (max-width: 980px){
  .works{
    padding: 6.25em 0 1.5em;
  }

  .work-detail{
    margin-top: 1.875em;
    padding: 1.875em 0 2.25em;
    &__items{
      gap: 1.875em;
    }
  }

  .info{
    padding: 1em 0 3.375em;
    &__desc{
      font-size: 1em;
      line-height: 138%;
    }
    &__nav{
      margin-top: 1.875em;
    }
  }
}


@media screen and (max-width: 576px){
  .works{
    .main__bottom{
      margin-top: 3em;
    }
  }

  .work-info{
    gap: 1.25em;
    &__desc{
      margin-top: .125em;
    }
  }

  .work-detail{
    &__img{
      &--hide-mob{
        display: none;
      }
      &--hide-desktop{
        display: block;
      }
    }
  }

  .info{
    &__desc{
      max-width: 21.875em;
    }
  }
}
