.pagination__btn,
.info__link{
  display: flex;
  align-items: center;
  margin-right: 3.125em;
  font-weight: 300;
  color: $dark;
  svg{
    width: 3.25em;
  }
  span{
    font-size: 1em;
    line-height: 119%;
  }
  &:hover{
    path{
      stroke: $primary;
    }
  }
}

.pagination{
  margin-top: 5.625em;
  display: flex;
  align-items: center;
  &__more{
    display: none;
  }
  &__prev{
    svg{
      margin-right: 1em;
    }
  }
  &__next{
    svg{
      margin-left: 1em;
    }
  }
}


@media screen and (max-width: 980px){
  .pagination{
    margin-top: 4.25em;
    justify-content: center;
    &__btn{
      display: none;
    }
    &__more{
      display: flex;
      padding: 0 3.25em;
      &:not(:hover){
        border-color: #E6E6E6;
      }
    }
  }
}
