.main{
  padding: 10.75em 0 4em;
  &__top{
    &-col1{
      max-width: 25em;
      padding-top: 1em;
    }
  }
  &__desc{
    font-size: 1.25em;
    line-height: 120%;
    color: #424344;
    @include grid100gap(.2em);
    span{
      line-height: inherit;
    }
  }
  &__bottom{
    margin-top: 9.75em;
    position: relative;
  }
}

.portfolio-items{
  row-gap: 5.625em;
}

.portfolio-card{
  display: block;
  &__img{
    position: relative;
    padding-bottom: 75.8%;
    img{
      position: absolute;
      top: 5em;
      left: 2.375em;
      width: calc(100% - 4.75em);
    }
  }
  &__content{
    margin-top: 1.875em;
    @include grid100gap(.25em);
  }
  &__desc{
    color: #9f9f9f;
    font-weight: 300;
  }
}

.text-bg{
  width: 100%;
  z-index: -1;
  pointer-events: none;
  img{
    width: 100%;
    display: block;
  }
  &--portfolio{
    position: absolute;
    top: 0;
    left: 0;
    margin-top: -9em;
  }
  &--service{
    position: absolute;
    top: 0;
    left: 0;
  }
  &--contacts{
    margin-top: -.1875em;
  }
}

.services{
  padding: 4em 0 5.1875em;
  &__inner{
    position: relative;
    padding-top: 8em;
  }
}

.feedback{
  padding: 5.1875em 0 2.25em;
  &__desc{
    line-height: 130%;
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-top: 2em;
  }
  &__link{
    font-size: 1.75em;
    line-height: 122%;
    @include lastM(0 0 .21em);
    display: block;
  }
}


@media screen and (max-width: 980px){
  .main{
    padding: 6em 0 1.875em;
    &__top{
      &-col1{
        padding-top: .5em;
      }
    }
    .h1{
      font-size: 2.375em;
    }
    &__desc{
      font-size: 1em;
      line-height: 119%;
      gap: .3125em;
    }
    &__bottom{
      margin-top: 5.625em;
    }
  }

  .portfolio-items{
    row-gap: 3.125em;
  }

  .portfolio-card{
    &__img{
      img{
        top: 2.625em;
        left: 1.25em;
        width: calc(100% - 2.5em);
      }
    }
    &__content{
      margin-top: 1em;
    }
  }

  .text-bg{
    &--portfolio{
      margin-top: -4.75em;
    }
    &--contacts{
      margin-top: .25em;
    }
  }

  .services{
    padding: 1.875em 0 2.25em;
    &__inner{
      padding-top: 5em;
      row-gap: .875em;
    }
  }

  .feedback{
    padding: 1.875em 0 2.125em;
    &__desc{
      line-height: 137%;
    }
    &__links{
      margin-top: 1em;
    }
    &__link{
      font-size: 1.25em;
    }
  }
}


@media screen and (max-width: 576px){
  .main{
    &__top{
      gap: 1.875em;
      &-col1{
        padding-top: 0;
        order: 2;
      }
    }
  }

  .text-bg{
    &--portfolio{
      margin-top: -2.625em;
    }
  }

  .services{
    &__inner{
      padding-top: 2.25em;
    }
  }
}
