.about{
  padding: 11.625em 0 2.5em;
  &__items{
    margin-top: 6.375em;
  }
}

.about-item{
  font-weight: 300;
  color: $dark;
  &__num{
    display: block;
    height: 4.875em;
  }
  &__text{
    margin-top: -2em;
    position: relative;
    z-index: 2;
  }
}

.specialization{
  margin-top: 9em;
  &__desc{
    font-weight: 300;
  }
  &__nav{
    display: flex;
    margin-top: 4.5em;
  }
  &__btn{
    padding: 0 2.5em;
  }
}


@media screen and (max-width: 980px){
  .about{
    padding: 6em 0 2em;
    &__items{
      margin-top: 4.25em;
      grid-template-columns: repeat(2,calc(50% - .9375em));
      gap: 1.875em;
    }
  }

  .about-item{
    max-width: 17.5em;
    &__icon{
      height: 3.625em;
    }
    &__text{
      margin-top: -1.625em;
      .text20{
        font-size: 1em;
      }
    }
  }

  .specialization{
    margin-top: 5.125em;
    grid-template-columns: 100%;
    &__nav{
      margin-top: 2.5em;
    }
  }
}


@media screen and (max-width: 576px){
  .about{
    &__items{
      padding: 0 1.25em;
      grid-template-columns: 100%;
    }
  }
}
