.footer{
  padding: 5.625em 0;
  &__logo{
    display: block;
    width: 100%;
    img{
      width: 100%;
      display: block;
    }
  }
  &__bottom{
    margin-top: 1.25em;
  }
  &__c{
    color: #555556;
    font-weight: 300;
  }
  &__links{
    display: flex;
  }
  &__link{
    font-size: 1em;
    line-height: 119%;
    @include lastM(0 2.25em 0 0);
  }
}




@media screen and (max-width: 980px){
  .footer{
    padding: 2.875em 0;
    &__bottom{
      margin-top: 1em;
    }
    &__c,
    &__link{
      font-size: .75em;
    }
    &__link{
      @include lastM(0 1.75em 0 0);
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    &__bottom{
      gap: 2.5em;
    }
    &__c,
    &__link{
      font-size: .875em;
    }
    &__links{
      flex-direction: column;
      align-items: flex-start;
      order: -1;
    }
    &__link{
      @include lastM(0 0 .5em);
    }
  }
}
