@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
@import "components/_pagination.scss";
@import "components/_tabs.scss";
@import "components/_grid.scss";
.m-title{
  *{
    line-height: inherit;
  }
  .lines{
    overflow: hidden;
  }
}

.heading-line{
  display: block;
  line-height: inherit;
  overflow: hidden;
  position: relative;
  &__inner{
    display: block;
    position: relative;
  }
  &__cover{
    background-color: #fff;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
  }
}

._scale-img{
  overflow: hidden;
  &:hover{
    img{
      transform: scale(1.05);
    }
  }
  img{
    transition: all .6s;
  }
}
