.service-item{
  padding: 11.625em 0 2.5em;
  .text-bg{
    margin-top: .34em;
  }
  &__desc{
    max-width: 67.75em;
    margin-top: 6em;
  }
  &__list{
    @include grid100gap(7.75em);
    margin-top: 6.625em;
  }
  &__bottom{
    margin-top: 7.75em;
  }
  &__hint{
    grid-column: 2/3;
    font-weight: 300;
    max-width: 35em;
    a{
      text-decoration: underline;
      white-space: nowrap;
    }
  }
}

.service-group{
  &__items{
    @include grid100gap(1.5625em);
  }
}

.product-item{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-bottom: 1.625em;
  border-bottom: 1px solid #666;
  font-weight: 300;
  &__desc{
    color: #999;
  }
}


@media screen and (max-width: 980px){
  .service-item{
    padding: 6em 0 2em;
    &__list{
      gap: 4.375em;
      margin-top: 4.5625em;
    }
    &__bottom{
      margin-top: 4.25em;
    }
    &__hint{
      grid-column: 1/-1;
    }
  }

  .service-group{
    grid-template-columns: 100%;
    gap: 1.5em;
  }
}


@media screen and (max-width: 576px){
  .service-item{
    &__desc{
      margin-top: 2em;
      .h2{
        font-size: 1.25em;
      }
    }
    &__hint{
      .text20{
        font-size: 1em;
      }
    }
  }

  .service-group{
    &__title{
      font-size: 1em;
    }
  }

  .product-item{
    &__title{
      font-size: 1em;
    }
    &__desc{
      font-size: .875em;
    }
  }
}
