.header{
  padding-top: 4.875em;
  position: relative;
  z-index: 10;
  &__inner{
    //display: flex;
    //justify-content: space-between;
  }
  &__logo{
    display: block;
    flex-shrink: 0;
    width: 13.5em;
    img{
      width: 100%;
      display: block;
    }
  }
}

.burger{
  display: none;
}

.menu,
.nav{
  display: flex;
  align-items: center;
}

.menu{
  justify-content: space-between;
}

.nav{
  //margin-right: 5em;
  &__link{
    margin-right: 2.5em;
    position: relative;
    &:hover{
      color: $black;
    }
    &:not(:hover){
      &:before{
        transform: translateX(-50%) scaleX(0);
      }
    }
    &._active{
      &:before{
        transform: translateX(-50%);
      }
    }
    span{
      font-size: 1.125em;
      line-height: 122%;
    }
    &:before{
      content: '';
      position: absolute;
      left: 50%;
      top: 100%;
      transform: translateX(-50%);
      width: 100%;
      height: 1px;
      margin-top: .125em;
      background: $black;
      transition: all .5s;
    }
  }
}

.menu-contacts{
  display: none;
}


@media screen and (max-width: 980px){
  .header{
    padding-top: 2.5em;
    &__inner{
      display: flex;
      align-items: center;
      justify-content: space-between;
      gap: unset;
    }
    &__logo{
      width: 10.75em;
      position: relative;
      z-index: 12;
    }
    &._open{
      .menu{
        opacity: 1;
        pointer-events: unset;
      }
      .burger{
        span{
          &:nth-child(1){
            transform: rotate(-45deg) translate(-5%,.1875em);
          }
          &:nth-child(2){
            transform: rotate(45deg)  translate(-5%,-.1875em);
          }
        }
      }
    }
  }

  .menu{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: #fff;
    z-index: 11;
    flex-direction: column;
    align-items: flex-start;
    padding: 12.5em 1.25em 3.5em;
    opacity: 0;
    pointer-events: none;
    transition: all .4s;
  }

  .nav{
    flex-direction: column;
    align-items: flex-start;
    margin: 0 0 2em;
    &__link{
      margin: 0 0 1.625em;
      font-size: 1.125em;
    }
  }

  .burger{
    width: 2em;
    height: 2em;
    @include center;
    flex-direction: column;
    position: relative;
    z-index: 12;
    span{
      display: block;
      width: 100%;
      height: .125em;
      background-color: $black;
      @include lastM(0 0 .3125em);
      border-radius: .25em;
      transition: all .5s;
    }
  }

  .menu-contacts{
    display: block;
    margin-bottom: 2.5em;
  }
}


@media screen and (max-width: 576px){
  .header{
    &__btn{
      //width: 100%;
    }
  }
}
