.contacts{
  padding: 11.625em 0 .625em;
  &__bottom{
    margin-top: 2.75em;
  }
  &__col{
    max-width: 37.5em;
  }
}

.contacts-form{
  @include grid100gap(.875em);
  margin-top: -1.5em;
  .m-check{
    max-width: 28.5em;
    margin-top: 1.3125em;
  }
  &__nav{
    margin-top: 2em;
    display: flex;
  }
  &__btn{
    height: 4.625em;
    padding: 0 3.25em;
    span{
      font-size: 1.25em;
    }
    &:hover{
      ._animate-span{
        width: 830px;
        height: 830px;
      }
    }
  }
}


@media screen and (max-width: 980px){
  .contacts{
    padding: 6em 0 2.75em;
    &__bottom{
      margin-top: 3.125em;
    }
  }

  .contacts-form{
    margin-top: -1.1875em;
    gap: 1em;
    .m-check{
      margin-top: .5em;
    }
    &__nav{
      margin-top: 1em;
    }
    &__btn{
      padding: 0 2.5em;
      height: 3.375em;
      span{
        font-size: .875em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .contacts{
    &__bottom{
      row-gap: 3.5em;
    }
  }
}
