.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 2.375em;
    height: 3.375em;
    border-radius: 3.75em;
    font-weight: 500;
    position: relative;
    overflow: hidden;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: .875em;
        line-height: 120%;
        &:not(._animate-span){
            position: relative;
            z-index: 2;
        }
    }
    ._animate-span{
        position: absolute;
        display: block;
        width: 0;
        height: 0;
        border-radius: 50%;
        transition: width 0.4s ease-in-out, height 0.4s ease-in-out;
        transform: translate(-50%, -50%);
        //z-index: -1;
    }
    &:hover{
        ._animate-span{
            width: 530px;
            height: 530px;
        }
    }
}

.m-btn-black-outline{
    color: $black;
    border: 1.5px solid $black;
    ._animate-span{
        background-color: $black;
    }
    &:hover{
        color: #fff;
        path{
            stroke: #fff;
        }
    }
}
