.modal-bg{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: $black;
    opacity: .5;
}

.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    //transition: all .3s;
    padding: 1em 0;
    text-align: center;

    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }
    &__dialog {
        text-align: left;
        vertical-align: middle;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;
        .modal__bg {
            display: block;
        }
    }
    &__dialog {
        background: #fff;
        width: 26em;
        max-width: 100%;
        position: relative;
        z-index: 3;
        padding: 3.5em 1.875em;
        display: inline-block;
    }
    &__top{
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    .close-btn{
        width: 1.4375em;
        @include center;
        position: absolute;
        top: 1.5em;
        right: 1.875em;
        svg{
            width: 100%;
        }
    }
}

.modal-form{
    margin-top: .625em;
    .m-check{
        margin-top: 1.5em;
        label{
            span{
                font-size: .75em;
            }
        }
    }
    &__nav{
        display: flex;
        margin-top: 2em;
    }
}



